html {
  scroll-behavior: smooth;
  font-size: calc((1vw + 1vh) * 0.33333);
}

body {
  font-family: "Gilroy";
  background-color: #85919D;
}

html,
body {
  min-height: 100%;
  position: relative;
}

body {
  font-size: 1rem;
}

.main {
  padding-top: 153px;
}

.w-ft {
  width: fit-content !important;
}

ul,
ol,
li,
p,
.h1, h1,
.h2, h2,
.h3, h3,
.h4, h4,
.h5, h5,
.h6 h6 {
  margin: 0;
}

h1,
.h1 {
  font-weight: 400;
  font-size: 8rem;
  line-height: 110%;
  color: var(--primary);
}

h2,
.h2 {
  font-weight: 400;
  font-size: 5rem;
  line-height: 100%;
}

h3,
.h3 {
  font-weight: 400;
  font-size: 3.2rem;
  line-height: 110%;
}

h4,
.h4 {
  font-weight: 600;
  font-size: 2.6rem;
  line-height: 100%;
}

h5,
.h5 {
  font-weight: 400;
  font-size: 2.4rem;
  line-height: 1.25em;
}

p,
.p {
  font-weight: 300;
  font-size: 2.2rem;
  line-height: 1.2em;
}

.fw-800 {
  font-weight: 800;
}

.fw-700 {
  font-weight: 700;
}

.fw-600 {
  font-weight: 600;
}

.fw-500 {
  font-weight: 500;
}

.fw-400 {
  font-weight: 400;
}

.fw-300 {
  font-weight: 300;
}

.text-primary {
