}

.text-primary-bg {
  color: #85919D !important;
}

.text-dark {
  color: #464A55 !important;
}

.text-light-dark {
  color: #717B80 !important;
}

.text-up {
  text-transform: uppercase;
}

.bg-light {
  background-color: #D1D0CE !important;
}

.mt-80 {
  margin-top: 8rem;
}

.mt-60 {
  margin-top: 6rem;
}

.mb-100 {
  margin-bottom: 10rem;
}

.mb-80 {
  margin-bottom: 8rem;
}

