.btn-primary {
  padding: 2rem 3rem;
  display: inline-block;
  background-color: rgba(255, 255, 255, 0.2);
  border: 2px solid #F5F5F5;
  font-weight: 400;
  font-size: 1.6rem;
  line-height: 1.9rem;
  color: #FFFFFF;
  letter-spacing: 0.165em;
  text-transform: uppercase;
  box-shadow: none;
  border-radius: 0;
  transition: 0.3s;
}
.btn-primary:hover, .btn-primary:active, :not(.btn-check) + .btn-primary:active, .btn-primary:first-child:active, .btn-primary.active, .btn-primary.show {
  border: #FFFFFF 2px solid !important;
  background-color: #FFFFFF !important;
  color: #85919D !important;
  box-shadow: none;
}

.btn-primary.mini {
  padding: 1rem 3rem;
}

.btn-primary.dark {
  background-color: #85919D !important;
}

.btn-primary.dark:hover {
  background-color: #F5F5F5 !important;
  color: #85919D;
}

