.map {
  position: relative;
  filter: grayscale(1);
  -ms-filter: grayscale(1);
  -webkit-filter: grayscale(1);
  -moz-filter: grayscale(1);
  -o-filter: grayscale(1);
}

.form-control {
  width: 100%;
  outline: none;
  padding: 15px 10px;
  background: rgba(255, 255, 255, 0.3);
  border: 1px solid #85919D;
  border-radius: 0;
  font-weight: 300;
  font-size: 2rem;
  line-height: 1.3em;
  color: #85919D;
}
.form-control:focus, .form-control:active {
  border: 1px solid #85919D;
  box-shadow: none;
}

.form-control::placeholder {
  font-weight: 300;
  font-size: 2rem;
  line-height: 1.3em;
  color: #85919D;
}

.custom-checkbox {
  margin-top: 6px;
  display: flex;
  align-items: center;
  flex-wrap: nowrap;
  column-gap: 2.4rem;
}

.custom-checkbox__label {
  position: relative;
  width: 28px;
  height: 28px;
  flex-shrink: 0;
}

.custom-checkbox__label input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
  z-index: 50;
}

.custom-checkbox__label::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #F5F5F5;
  border: 1px solid #85919D;
  z-index: 10;
  pointer-events: none;
}

.custom-checkbox__label .checkmark {
  opacity: 0;
  position: absolute;
  left: 0;
  bottom: 3px;
  width: 100%;
  height: 100%;
  background: url("../img/check.png") no-repeat bottom center/contain;
  z-index: 20;
  pointer-events: none;
}

.custom-checkbox__label input:checked ~ .checkmark {
  opacity: 1;
}

.custom-checkbox__text {
  font-weight: 300;
  font-size: 1.4rem;
  line-height: 1.3em;
  letter-spacing: 0.02em;
  color: #85919D;
}

@media (max-width: 991px) {
  .main {
    padding-top: 80px;
  }
  .btn-primary {
    font-size: 16px;
    padding: 20px 12px;
  }
  h2,
  .h2 {
    font-size: 28px;
  }
  h3,
  .h3 {
    font-size: 16px;
  }
  h5,
  .h5 {
    font-size: 12px;
  }
  p,
  .p {
    font-size: 16px;
  }
  .form-control {
    font-size: 16px;
    padding: 12px 8px;
  }
  .form-control::placeholder {
    font-size: 16px;
  }
  .custom-checkbox__label {
    width: 22px;
    height: 22px;
