.preload-wrap {
  position: fixed;
  z-index: 10000;
  left: 0;
  top: 0;
  height: 100vh;
  width: 100vw;
  display: none;
  align-items: center;
  justify-content: center;
  background-color: #85919D;
  transition: all 1s ease-out;
}

.preload-wrap svg {
  max-width: 90%;
  max-height: 90%;
}

.preload-wrap.hidden {
  transform: translateY(-100%);
}

@media (max-width: 991px) {
  .preload-wrap {
    display: flex;
  }
}
.slider-control {
  margin-inline: auto;
  margin-top: 5rem;
  width: fit-content;
  display: flex;
  align-items: center;
  column-gap: 5rem;
}

.slider__navigation {
  position: static;
}
.slider__navigation-prev, .slider__navigation-next {
  height: 4.8rem;
  width: 4.8rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  border: 1px solid #F5F5F5;
  background-color: #BCBBBA;
  flex-shrink: 0;
}
.slider__navigation-prev::after {
  content: "";
  display: inline-block;
  border: solid #F5F5F5;
  border-width: 0 3px 3px 0;
  border-radius: 2px;
  padding: 7px;
  transform: rotate(135deg) translateX(-2px) translateY(-2px);
  transition: 0.3s;
}
.slider__navigation-next::after {
  content: "";
  display: inline-block;
  border: solid #F5F5F5;
  border-width: 0 3px 3px 0;
  border-radius: 2px;
  padding: 7px;
  transform: rotate(-45deg) translateX(-2px) translateY(-2px);
  transition: 0.3s;
}

.slider__navigation-prev.dark, .slider__navigation-next.dark {
  background-color: #9BA5AF;
}

.slider__pagination .swiper-pagination-bullet {
  opacity: 1;
  background-color: transparent;
  border: 1px solid #F5F5F5;
}
.slider__pagination .swiper-pagination-bullet-active {
  background-color: #D9D9D9;
  opacity: 1;
}
.slider__pagination.dark .swiper-pagination-bullet-active {
  background-color: #85919D;
}

@media (max-width: 991px) {
  .slider__navigation {
    position: static;
  }
  .slider__navigation-prev, .slider__navigation-next {
    height: 45px;
    width: 45px;
  }
  .slider-control {
    width: 100%;
    justify-content: space-between;
  }
  .slider-control .slider__pagination {
    width: fit-content;
  }
}
