.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 3.4rem;
}
.pagination li {
  color: #F5F5F5;
  text-align: center;
  font-size: 3rem;
  line-height: 110.5%;
  text-transform: uppercase;
  cursor: pointer;
}
.pagination li.pagination-button {
  display: flex;
  width: 6rem;
  height: 6rem;
  justify-content: center;
  flex-shrink: 0;
  align-items: center;
  border-radius: 50%;
  border: 1px solid #F5F5F5;
  background: transparent;
  transition: 0.4s;
}
.pagination li.pagination-button a {
  text-decoration: none;
  color: inherit;
}
.pagination li.pagination-button:hover {
  background-color: #464A55;
}
.pagination li.pagination-button.active {
  background-color: #464A55;
}

@media (max-width: 768px) {
  .pagination {
    column-gap: 14px;
  }
  .pagination li {
    font-size: 9px;
  }
  .pagination li a {
    text-decoration: none;
    color: inherit;
  }
  .pagination li.pagination-button {
    width: 25px;
    height: 25px;
  }
}
